<template>
  <div class="container login-container">
    <div class="card register col-md-9 m-auto bg-gray-1">
      <div class="card-body">
        <div class="alert alert-success" role="alert" v-if="flashMsg">{{flashMsg}}</div>
        <div class="alert alert-danger" role="alert" v-if="error">{{error}}</div>
        <h1 class="text-center text-primary mt-3 mb-3">Đăng nhập</h1>

        <div class="form-group">
          <label for="email">Email address</label>
          <input
            type="email"
            class="form-control"
            aria-describedby="emailHelp"
            placeholder="Enter email"
            v-model="email"
          />
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input type="password" class="form-control" placeholder="Password" v-model="password" />
        </div>
        <button class="btn btn-primary btn-block mt-4 mb-4" v-if="!isLoading" @click="login">Login</button>
        <button class="buttonload btn btn-primary btn-block mt-4 mb-4" v-else>
          <i class="fa fa-spinner fa-spin mr-2"></i>Loading
        </button>
        <p class="mt-3">
          Chưa có tài khoản?
          <router-link to="/register">Đăng ký</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "Login",
  computed: {
    ...mapGetters(["isLoading"]),
  },
  data() {
    return {
      email: "",
      password: "",
      flashMsg: "",
      error: "",
    };
  },
  methods: {
    async login() {
      this.$store.dispatch("setLoading", true);
      let response = await axios.post("/api/login", {
        email: this.email,
        password: this.password,
      });
      this.$store.dispatch("setLoading", false);
      if (response.data.isError) {
        this.flashMsg = "";
        this.error = response.data.error;
      } else {
        this.$bus.emit("loggedin");
        this.error = "";
        if (response.data.msg === "notVerified") {
          this.$router.push("/verification");
        }
      }
    },
  },
  created() {
    if (this.$route.query.err === "authenticateFail") {
      this.error = "Bạn cần đăng nhập để làm được việc này";
    } else if (this.$route.query.msg === "registerSuccess") {
      this.flashMsg = "Đăng ký thành công. Hãy đăng nhập !";
    }
  },

  beforeCreate() {
    this.forwardAuthenticate();
  },
};
</script>

<style lang="scss">
.register {
  border: none;
}
</style>